<template>
  <div class="cont">
    <div class="top">
      <div style="margin-left: 0.466rem">
        <img :src="staffInfo.headimgurl" alt />
      </div>
      <div style="margin-left: 0.32rem">
        <div style="display: flex; align-item: center">
          <div class="name" style="margin-top: 0">{{ staffInfo.nickname }}</div>
          <div class="add">{{ staffInfo.company }}</div>
        </div>
        <p class="name">{{ staffInfo.mobile }}</p>
      </div>
    </div>
    <div class="nav">
      <ul class="clear">
        <li @click="goOrder">
          <img src="../img/fwqd1.png" alt />
          <p>订单统计</p>
        </li>
        <li @click="goSetting">
          <img src="../img/shezhi.png" alt />
          <p>设置</p>
        </li>
      </ul>
    </div>
    <div class="main">
      <div class="order">
        <p class="tit">待服务订单</p>
        <div v-if="list.length == 0" class="no">
          <img src="../img/zwqd.png" alt />
          <p>暂无订单</p>
        </div>
        <ul v-else>
          <li v-for="(item, index) in list" :key="index">
            <div
              v-if="
                item.source == 2 &&
                item.payment_type == '商家收款' &&
                item.pay_status == 0
              "
              class="pay_btn"
              @click="goPay(item.id, item.pay_amount)"
            >
              收款
            </div>
            <div
              v-if="item.service_status == 3"
              class="pay_btn"
              @click="finish(item.id)"
            >
              确认完成
            </div>
            <p class="clear">
              <span class="fl">客服预约：</span>
              <span class="fr"
                >{{ item.service_time
                }}{{ item.service_time != "" ? "之前" : "" }}</span
              >
            </p>
            <p class="clear" v-if="item.business_service_time != ''">
              <span class="fl">商家预约：</span>
              <span class="fr">{{ item.business_service_time }}</span>
            </p>
            <p class="clear">
              <span class="fl">服务项目：</span>
              <span class="fr">{{ item.service_name }}</span>
            </p>
            <p class="clear">
              <span class="fl">服务数量：</span>
              <span class="fr">{{ item.number }}</span>
            </p>
            <p class="clear">
              <span class="fl">联系人：</span>
              <span class="fr" style="width: 75%; text-align: right">{{
                item.contact_name
              }}</span>
            </p>
            <p class="clear">
              <span class="fl">联系电话：</span>
              <span class="fr">{{ item.contact_mobile }}</span>
            </p>
            <p class="clear">
              <span class="fl">服务地址：</span>
              <span class="fr" style="width: 75%; text-align: right">{{
                item.address
              }}</span>
            </p>
            <p class="clear" v-if="item.claim != ''">
              <span class="fl">特殊说明：</span>
              <span class="fr" style="width: 75%; text-align: right">{{
                item.claim
              }}</span>
            </p>
            <div class="bott clear">
              <dl>
                <dd v-if="item.business_service_time != ''" :style="width1">
                  <a
                    :href="'tel:' + item.contact_mobile"
                    style="
                      font-size: 0.4rem;
                      color: #5fe1db;
                      border-right: 1px solid #37cfc8;
                    "
                    >联系用户</a
                  >
                </dd>
                <dd v-else :style="width">
                  <a
                    :href="'tel:' + item.contact_mobile"
                    style="
                      font-size: 0.4rem;
                      color: #5fe1db;
                      border-right: 1px solid #37cfc8;
                    "
                    >联系用户</a
                  >
                </dd>
                <dd v-if="item.business_service_time == ''" :style="width">
                  <a
                    href="javascript:;"
                    style="
                      font-size: 0.4rem;
                      color: #5fe1db;
                      border-right: 1px solid #37cfc8;
                    "
                    @click="popBookTime(item.id)"
                    >预约时间</a
                  >
                </dd>
                <dd v-if="item.business_service_time != ''" :style="width1">
                  <a
                    href="javascript:;"
                    style="font-size: 0.4rem; color: #37cfc8"
                    @click="popMap(item.address)"
                    >导航</a
                  >
                </dd>
                <dd v-else :style="width">
                  <a
                    href="javascript:;"
                    style="font-size: 0.4rem; color: #37cfc8"
                    @click="popMap(item.address)"
                    >导航</a
                  >
                </dd>
              </dl>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- 商家预约时间 -->
    <popup v-model="startPicker" position="bottom" :overlay="true">
      <datetime-picker
        v-model="startDate"
        title="选择预约时间"
        :minDate="minDate"
        type="datetime"
        @cancel="startPicker = false"
        @confirm="onStartConfirm"
      />
    </popup>
  </div>
</template>

<script>
import { Toast, DatetimePicker, Popup, Dialog } from "vant";
import LocationSdk from "../commons/location-sdk";
// import BMap from "BMap";
export default {
  components: { Toast, DatetimePicker, Popup, Dialog },
  data() {
    return {
      id: 0,
      staff_token: "",
      staffInfo: {},
      location: "",
      status_code: 2, // 待上门
      list: [],
      minDate: new Date(),
      startPicker: false,
      startDate: new Date(),
      width: "width:33%",
      width1: "width:49%",
      // border:'border-right: 1px solid #eee'
    };
  },
  methods: {
    goPay(id, pay_amount) {
      this.$router.push({
        path: "/collection",
        query: {
          staff_token: this.staff_token,
          order_id: id,
          pay_amount: pay_amount,
        },
      });
    },
    finish(id) {
      Dialog.confirm({
        title: "提示",
        message: "是否确认完成",
      })
        .then(() => {
          this.id = id;
          this.api
            .finish({
              params: {
                id: this.id,
                staff_token: this.staff_token,
              },
            })
            .then((res) => {
              if (res.code != 0) {
                Toast(res.message);
                return;
              }
              this.getOrder();
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    goSetting() {
      this.$router.push({ path: "set_Up" });
    },
    goOrder() {
      this.$router.push({ path: "orderCount" });
    },
    // 获取待上门订单
    getOrder() {
      this.api
        .orderCount({
          params: {
            staff_token: this.staff_token,
            status_code: this.status_code,
            order_by: "service_time",
          },
        })
        .then((res) => {
          if (res.code != 0) {
            Toast(res.message);
            return;
          }

          this.list = res.content.list;
       
        });
    },
    popBookTime(id) {
      this.id = id;
      this.startPicker = true;
    },
    onStartConfirm(picker, value, index) {
      this.startPicker = false;
      this.setBookTime();
    },
    setBookTime() {
      this.api
        .orderBookTime(this.id, {
          business_service_time: this.startDate,
          staff_token: this.staff_token,
        })
        .then((res) => {
          Toast("成功添加预约时间！");
          if (res.code != 0) {
            Toast(res.message);
            return;
          }
          this.getOrder();
        });
    },

    popMap(address) {
      var geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all",
      });
      geocoder.getLocation(address, function (status, result) {
        if (status === "complete" && result.info === "OK") {
          if (result && result.geocodes) {
            (window.location.href =
              "https://uri.amap.com/marker?position=" +
              result.geocodes[0].location.lng +
              "," +
              result.geocodes[0].location.lat),
              "&name=" + address;
          }
        }
      });
      // 创建地址解析器实例
      // var myGeo = new BMap.Geocoder();
      // // 将地址解析结果显示在地图上，并调整地图视野
      // myGeo.getPoint(address, function(point) {
      //   if (point) {
      //     // map.centerAndZoom(point, 16);
      //     // map.addOverlay(new BMap.Marker(point));
      //     let location = point.lat + "," + point.lng;

      //     window.location.href =
      //       "https://api.map.baidu.com/marker?location=" +
      //       location +
      //       "&title=服务地址&content=" +
      //       address +
      //       "&output=html&src=webapp.baidu.openAPIdemo";
      //   }
      // });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:#f2f2f2");
    document
      .getElementById("app")
      .setAttribute("style", "background-color:#f2f2f2");
    this.staff_token = this.$route.query.staff_token;
    localStorage.setItem("staff_token", this.staff_token);
    this.api
      .staffInfo({
        params: { staff_token: this.staff_token },
      })
      .then((res) => {
        if (res.code != 0) {
          Toast(res.message);
          return;
        }

        this.staffInfo = res.content.info;
      });
    this.getOrder();
  },
};
</script>

<style scoped>
.top {
  background: #37cfc8;
  padding-top: 0.4667rem;
  padding-bottom: 0.8rem;
  text-align: center;
  display: flex;
  position: relative;
  align-items: center;
}
.top img {
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  margin: auto;
  border: 0.2667rem solid #fff;
  border-radius: 50%;
}
.top .name {
  font-size: 0.4267rem;
  color: #fff;
  margin-top: 0.3333rem;
  text-align: left;
}
.top .add {
  width: 3.4rem;
  height: 0.5333rem;
  line-height: 0.5333rem;
  text-align: center;
  color: #666;
  font-size: 0.32rem;
  background: #fff;
  margin-left: 0.253rem;
  border-radius: 0.253rem;
}
.main {
  padding: 0 0.3333rem;
  width: 100%;
  /* position: absolute;
    top: 4.4rem; */
  /* background: #f2f2f2 url(../img/smbg.png) 0 top no-repeat; */
  background-size: 100%;
  box-sizing: border-box;
  padding-bottom: 0.2667rem;
}
.nav {
  background: #fff;
  padding-left: 0.84rem;
}

.nav ul li {
  text-align: center;
  float: left;
  color: #666;
  font-size: 0.4rem;
  padding: 0.5333rem 0;
  position: relative;
  margin-right: 1rem;
}
/* .nav ul li:first-child::after {
  display: block;
  content: "";
  width: 1px;
  height: 1.24rem;
  border-left: 1px solid #eee;
  position: absolute;
  top: 1.0667rem;
  right: 0;
} */
.nav ul li img {
  width: 1.44rem;
  height: 1.44rem;
}
.nav ul li p {
  color: #666;
  font-weight: 400;
  font-size: 0.37333rem;
  padding-top: 0.1333rem;
}
.no {
  text-align: center;
  margin-top: 1.3333rem;
}
.no p {
  color: #9b9b9b;
  font-size: 0.4rem;
}
.no img {
  width: 1.866rem;
  height: 1.866rem;
}
.order .tit {
  color: #333;
  font-size: 0.37333rem;
  font-weight: 500;
  padding-left: 0.15rem;
  border-left: 0.08rem solid #37cfc8;
  margin-top: 0.6rem;
  margin-bottom: 0.2rem;
}
.order ul li {
  background: #fff;
  padding: 0.5333rem 0;
  margin-top: 0.2667rem;
  border-radius: 0.16rem;
  padding-bottom: 0;
}
.order ul li p {
  margin-bottom: 0.4933rem;
  padding: 0 0.4rem;
}
.order ul li p:last-child {
  margin-bottom: 0;
}
.order span {
  font-size: 0.373rem;
  color: #666;
  font-weight: 400;
}
.order ul li dd .pho {
  width: 0.3067rem;
  height: 0.3067rem;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.2rem;
}
.order ul li dd .daoh {
  width: 0.3067rem;
  height: 0.3167rem;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.2rem;
}
.bott {
  background: #dcf4f3;
  border-radius: 0 0 0.16rem;
}
.bott dd {
  float: left;
  text-align: center;
  position: relative;
  /* border-right: 1px solid #eee */
}
/* .bott dd:first-child::after{
    display: block;
    content: '';
    width: 1px;
    height: 100%;
    border-left: 1px solid #eee;
    position: absolute;
    top: 0;
    right: 0;
} */
/* .bott dd:nth-child(2)::after{
    display: block;
    content: '';
    width: 1px;
    height: 100%;
    border-left: 1px solid #eee;
    position: absolute;
    top: 0;
    right: 0;
} */
.bott dd a {
  display: block;
  margin: 0.4rem 0;
}
.pay_btn {
  width: 2.493rem;
  height: 0.853rem;
  line-height: 0.835rem;
  text-align: center;
  background: rgba(55, 207, 200, 1);
  border-radius: 0.44rem;
  font-size: 0.373rem;
  font-weight: 400;
  color: #fff;
  float: right;
  margin-right: 0.4rem;
  margin-bottom: 0.6133rem;
}
</style>



