<template>
    <div class="detail">
        <div class="head">
            <div class="nav">
                <ul class="tab-tilte clear">
                    <li @click="choose(0)" :class="{active:cur==0}">全部</li>
                    <li @click="choose(1)" :class="{active:cur==1}">待上门</li>
                    <li @click="choose(2)" :class="{active:cur==2}">已完成</li>
                </ul>
            </div>
            <div class="choise clear">
                <p @click="startPicker = true">{{ startDate | formateD }} <img src="../img/ri.jpg" alt=""></p>
                &nbsp;至&nbsp;
                <p @click="endPicker = true">{{ endDate | formateD }} <img src="../img/ri.jpg" alt=""></p>
            </div>
             <!-- 起始时间选择器 -->
            <popup v-model="startPicker" position="bottom" :overlay="true">
                <datetime-picker
                v-model="startDate"
                title="选择起始日期"
                :maxDate="maxDate"
                :minDate="minDate"
                type="date"
                @cancel="startPicker = false"
                @confirm="onStartConfirm"
                />
            </popup>

             <!-- 截止时间选择器 -->
            <popup v-model="endPicker" position="bottom" :overlay="true">
                <datetime-picker
                v-model="endDate"
                title="选择截止日期"
                :maxDate="maxDate"
                :minDate="minDate"
                type="date"
                @cancel="endPicker = false"
                @confirm="onEndConfirm"
                />
            </popup>

            <p class="mingxi">合计金额：{{ amount_total }}元&nbsp;&nbsp;&nbsp;&nbsp;共{{ total }}条记录</p>

        </div>
         <div class="tab-content">
            <div>
                <ul>
                    <li v-for="item in list" :key="item.sn">
                        <div class="top clear">
                            <span class="time fl">订单号码：{{ item.sn }}</span>                           
                        </div>
                        <div class="nr clear">
                            <div class="left fl">
                                <p>派单时间：{{ item.dispatch_time }}</p>
                                <p>服务项目：{{ item.service_name }}</p>
                                <p>服务数量：{{ item.number }}</p>
                                <p>订单收益：{{ item.settlement_amount }}元</p>
                                <p>预约时间：{{ item.service_time }}</p>
                                <p>服务地址：{{ item.address }}</p>
                            </div>
                            <div class="right fr">
                                <p v-if="item.status_code == 2" class="status">待上门</p>
                                <p v-if="item.status_code == 3" class="status">已完成</p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import {Toast,DatetimePicker,Popup,Picker} from 'vant';
import { formatDate } from '@/utils/index'
import moment from "moment"
export default {
    components:{Toast,DatetimePicker,Popup,Picker},
    data(){
        return{
            staff_token: '',
            cur:0,
            status_code: '',
            page: 1,
            list: [],
            total: 0,
            amount_total: 0,
            // 日期选择
            minDate: new Date(2019, 0, 1),
            maxDate: new Date(),
            startPicker: false,
            startDate: new Date(),
            endPicker: false,
            endDate: new Date(),
        }
    },
    filters: {
            formateD(param) {
              if (param == "2019年5月6日") {
                return '2019年5月'
              }
              var shij = new Date(param);
                return formatDate(shij, 'yyyy年MM月dd日' )
            },
        },
    methods:{
        onStartConfirm(picker, value, index) {    
            this.startPicker = false;
            this.getOrder()
        },
       
        onEndConfirm(picker, value, index) {    
            this.endPicker = false;
            this.getOrder()
        },
        getOrder() {
            this.api
            .orderCount({
                params: { 
                    staff_token: this.staff_token,
                    status_code: this.status_code,
                    start_date: moment(this.startDate).format("YYYY-MM-DD"),
                    end_date: moment(this.endDate).format("YYYY-MM-DD"),
                    page: this.page
                    }
            })
            .then(res => {
                    
                if (res.code != 0) { 
                    Toast(res.message)
                    return
                }

                this.list = res.content.list
                this.total = res.content.total
                this.amount_total = res.content.amount_total
            });
        },

        choose(cur){
            this.cur=cur
            switch (cur) {
                case 1:
                    this.status_code = 2
                    break;
                case 2:
                    this.status_code = 3
                    break;
            
                default:
                    this.status_code = ''
                    break;
            }
            this.getOrder()
        }
    },
    mounted(){
        // 月初
        var now = new Date()
        var nowYear = now.getFullYear()
        var nowMonth = now.getMonth() 
        this.startDate = new Date(nowYear, nowMonth, 1)
        this.endDate = moment(this.endDate).endOf('month').format("YYYY-MM-DD");
        document.querySelector('body').setAttribute('style', 'background-color:#f2f2f2')
        this.staff_token = localStorage.getItem("staff_token")
        this.getOrder()
    }
}
</script>

<style scoped>
.head{
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 999;
}
.nav{
    background: #fff
}
ul.tab-tilte li {
    margin: 0;
    padding: 0.4133rem 0;
    list-style: none;
    background: #fff
}
.tab-tilte li{
    float: left;
    width: 22%;
    text-align: center;
    font-size: 0.4rem;
    color: #666;
    position: relative;
}
/* 点击对应的标题添加对应的背景颜色 */
.tab-tilte .active{  
    color: #5fe1db;
}
.tab-tilte .active::after{
    content: "";
    display: block;
    width: 65%;
    height: 0.0533rem;
    border-bottom: 0.0533rem solid #5fe1db;
    position: absolute;
    bottom: 0;
    left: 18%;
}
.tab-content{
    margin-top: 3.5rem;
}
.tab-content ul li{
    margin: 0.2667rem 0.3333rem;
    background: #fff;
}
.choise{
    padding: 0.4rem 0.4rem 0.5333rem 0.4rem;
    background: #f2f2f2;
    color: #5fe1db;
    font-size: 0.3733rem;
}
.choise p{
    display: inline;
}
.choise img {
    display: inline-block;
    width: 0.5rem;
    height: 0.2rem;
}
.mingxi{
    font-size: 0.32rem;
    color: #666;
    padding-left: .4rem;
    height: 0.7333rem;
    line-height: 0.7333rem;
    background: #f2f2f2
}
.tab-content ul li{
    margin: 0.2667rem 0.3333rem;
    background: #fff;
}
.top{
    padding: 0.5333rem 0.2667rem;
    border-bottom: 0.0133rem solid #ddd
}
.top .time{
    font-size: 0.3733rem;
    color: #333;
}
.nr{
    padding: 0.5333rem 0.2667rem;
    padding-bottom: 0.2667rem;
    font-size: 0.3733rem;
    color: #666;
}
.nr .left{
    width: 75%;
    border-right: 0.0133rem solid #ddd
}
.nr .right{
    width: 22%;
    text-align: center
}
.nr .right .status{
    font-size: 0.4rem;
    color: #5FE1DB;
    margin-top: 65%
}
.nr p{
    margin-bottom: 0.2667rem
}

</style>



