<template>
  <div class="pay_box">
    <div class="pay_top">
      <div class="pay_text">
        <span>订单金额</span>
        <div class="coll_amount">
          <span>￥</span>
          <span style="font-size:0.6133rem">{{this.$route.query.pay_amount}}</span>
        </div>
      </div>
      <div id="qrcode" ref="qrcode" class="qrcode"></div>

      <span class="toast">用户可直接扫码支付</span>
    </div>

    <div class="wx_pay">
      <div class="pay_left">
        <img src="../img/icon_weix.png" alt />
        <span>商家微信支付：</span>
      </div>

      <div @click="getPay()" class="pay_right">
        <span>立即支付</span>
        <img src="../img/green_right_arrow.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import QRCode from "qrcodejs2";
import wxPay from "../commons/wx-sdk";
export default {
  components: { Toast },
  name: "collection",
  data() {
    return {
      staff_token: "",
      timer: null,
      detailList: {}
    };
  },

  watch: {},
  beforeDestroy() {
    clearInterval(this.timer);

    this.timer = null;
  },
  created() {
    document.title = "收款";
    this.staff_token = this.$route.query.staff_token;
  },
  mounted() {
    document
      .getElementsByTagName("body")[0]
      .setAttribute(
        "style",
        "background:linear-gradient(139deg,rgba(94,213,179,1) 0%,rgba(55,207,200,1) 100%);"
      );
    document
      .getElementById("app")
      .setAttribute("style", "background-color:transparent");
    this.init();
     this.timer = window.setInterval(() => {
       this.getPayStatus();
     }, 1000);
  },
  methods: {
    qrcode(url) {
      let qrcode = new QRCode("qrcode", {
        width: 200, // 设置宽度，单位像素
        height: 200, // 设置高度，单位像素
        text: url // 设置二维码内容或跳转地址
      });
    },
    init() {
      // 订单详情
      var baseUrl;
      if (document.domain === "daojia.colourlife.com") {
        baseUrl = "https://daojia.colourlife.com";
      } else {
        baseUrl = "https://daojia-czytest.colourlife.com";
      }
      this.qrcode(
        baseUrl +
          "/user/wechat/qr/payPage/" +
          this.$route.query.order_id +
          "?is_qr=1&is_staff=1&pay_amount="+this.$route.query.pay_amount
      );
    },
    getPayStatus() {
      this.api
        .payStatus({
         params:{
            order_id: this.$route.query.order_id
         }
        })
        .then(res => {
          if (res.code == 0) {
            if (res.content.pay_status ==1) {
              this.$router.push({
                path: "/paySuccess",
                query: {
                  staff_token: this.$route.query.staff_token,
                  order_id:  this.$route.query.order_id,
                  pay_amount:this.$route.query.pay_amount,
                  type:1
                }
              });
            }
          }
        });
    },
    getPay() {
      var baseUrl;
      if (document.domain === "daojia.colourlife.com") {
        baseUrl = "https://daojia.colourlife.com";
      } else {
        baseUrl = "https://daojia-czytest.colourlife.com";
      }
      window.location.href =
        baseUrl +
        "/user/wechat/qr/payPage/" +
        this.$route.query.order_id +
        "?is_qr=0&is_staff=1&pay_amount="+this.$route.query.pay_amount
    }
  }
};
</script>


<style>
</style>
<style  >
.pay_box {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: PingFangSC-Regular, PingFangSC;
}
.pay_top {
  width: 8.48rem;
  height: 11.24rem;
  background: url("../img/fukm_bg.png") center no-repeat;
  background-size: 100% 100%;
  margin-top: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.pay_text {
  font-size: 0.373rem;
  justify-content: center;
  display: flex;
  margin-top: 0.6133rem;
  flex-direction: column;
  align-items: center;
  color: #333;
  font-weight: 400;
}
.coll_amount {
  margin-top: 0.106rem;
  
  font-size: 0.4rem;
  display: flex;
  align-items: center;
  font-weight: 500;
  color: #f74646;
}
.qrcode {
  width: 5.4933rem;
  height: 5.4933rem;
  margin-top: 1.70666rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.toast {
  font-size: 0.3733rem;
  font-weight: 400;
  color: #666666;
  margin-top: 0.5866rem;
}
.wx_pay {
  font-size: 0.4rem;
  margin-top: 0.8rem;
  width: 7.4133rem;
  height: 1.426rem;
  line-height: 1.426rem;
  background: rgba(93, 232, 226, 1);
  border-radius: 0.853rem;
  display: flex;
  justify-content: space-between;
  padding: 0 0.533rem;
  color: #333333;
  font-weight: 400;
}
.pay_left {
  display: flex;
  align-items: center;
}
.pay_left img {
  width: 0.666rem;
  height: 0.586rem;
  margin-right: 0.266rem;
}
.pay_right {
  display: flex;
  align-items: center;
  font-size: 0.34666rem;
  color: #1caaa3;
  font-weight: 400;
}
.pay_right img {
  width: 0.1733rem;
  height: 0.346rem;
  margin-left: 0.1733rem;
}
</style>



