<template>
  <div class="pay_box">
    <img class="success_icon" src="../img/icon_success.png" alt />
    <span class="success_text">{{$route.query.type ==1 ?'收款':'付款'}}成功</span>
    <div class="amount">
      <span>￥</span>
      <span style="font-size:0.8rem">{{this.$route.query.pay_amount}}</span>
    </div>
    
    <div class="btn btn3" @click="goHome">返回首页</div> 
  </div>
</template>

<script>
export default {
  name: "paySuccess",
  data() {
    return {
      staff_token: ""
    };
  },

  watch: {},

  created() {
    document.title = "收款成功";
    this.staff_token =  localStorage.getItem("staff_token");
  },
  mounted() {
    document
      .getElementsByTagName("body")[0]
      .setAttribute("style", "background:#FFFFFF");
  },
  methods: {
    goHome() {
      this.$router.push({
        path: "/home",
        query: {
          staff_token: this.staff_token
        }
      });
    },
    goOrderList() {
      this.$router.push({
        path: "/orderList",
        query: {
          cur: 0
        }
      });
    },
    goOrderDetail() {
      this.$router.push({
        path: "/orderDetail",
        query: { id: orderid }
      });
    }
  }
};
</script>


<style>
</style>
<style  >
.pay_box {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: PingFangSC-Regular, PingFangSC;
}
.success_icon {
  width: 1.54666rem;
  height: 1.54666rem;
  margin-top: 2.0933rem;
}
.success_text {
  font-size: 0.4266rem;
  color: #333;
  font-weight: 400;
  margin-top: 0.32rem;
}
.amount {
  margin-top: 0.533rem;
  height: 1.12rem;
  line-height: 1.12rem;
  font-size: 0.4266rem;
  display: flex;
  font-weight: 600;
  color: #333;
}
.pay_box .btn {
  width: 5.86666rem;
  height: 1.28rem;
  line-height: 1.28rem;
  background: rgba(55, 207, 200, 1);
  border-radius: 0.64rem;
  font-weight: 400;
  font-size: 0.4rem;
  text-align: center;
}
.pay_box .btn1 {
  margin-top: 1.8133rem;
  color: #fff;
  background: #37cfc8;
}
.pay_box  .btn2 {
  margin-top: 0.4266rem;
  color: #37cfc8;
  background: #fff;
  border: 0.026rem solid rgba(55, 207, 200, 1);
}
.pay_box  .btn3 {
  margin-top: 1.8133rem;
  color: #37cfc8;
  background: #fff;
  border: 0.026rem solid rgba(55, 207, 200, 1);
}
</style>



