<template>
    <div class="setUp">
        <ul>
            <li class="clear" @click="goUpdate">
                <span>修改密码</span>
                <i><img src="../img/more.png" alt=""></i>
            </li>
            <li class="clear" @click="popUnbind">
                <span>解绑微信号</span>
                <i><img src="../img/more.png" alt=""></i>
            </li>
            <li class="clear">
                <a href="tel:0755-21002477">
                    <span>联系客服</span>
                    <span class="pho">0755-21002477</span>
                </a>              
            </li>
        </ul>
        <p class="tip">提示：需要修改注册手机号请联系客服人员处理。</p>

        <!-- 弹窗 -->
      <div class="confirm_unbind" v-show="show">
            <div class="confirm_con">
                <p class="tit">确认解绑微信?</p>
                <button class="cancel fl" @click="show=false">取消</button>
                <button class="save fr" @click="confirm">确认</button>
            </div>
      </div>
    </div>
</template>

<script>
import {Toast} from 'vant';
export default {
components:{Toast},
    data(){
        return {
            staff_token: '',
            show: false,
        }
    },
    methods:{
        confirm() {
            this.api.unbind({
                params: {
                    staff_token: this.staff_token,
                }
            }).then(res => {
                if (res.code != 0) {
                     Toast.fail(res.message);
                     return
                }
                Toast("解绑成功!");
                setTimeout(() => {
                    this.$router.push({ path: 'login', query: {staff_token: this.staff_token} })
                }, 1500)
                
            });

        },
        popUnbind() {
            this.show = true
        },
        goUpdate() {
            this.$router.push({ path: 'updatePassword' })
        }
     },
    mounted(){
        document.querySelector('body').setAttribute('style', 'background-color:#f2f2f2');
        this.staff_token = localStorage.getItem("staff_token");
    }
}
</script>

<style scoped>
.setUp ul{
    background: #fff
}
ul li{
    border-bottom: 1px solid #ddd;
    padding: .4rem 0.5333rem
}
ul li span{
    font-size: 0.4rem;
    color: #333b46;
    float: left;
}
ul li span.pho{
    color: #5FE1DB;
    float: right;
}
ul li i img{
    width: 10px;
    height: 13px;
    float: right;
}
.tip{
    font-size: 0.3467rem;
    color: #999;
    text-align: center;
    margin-top: 0.1333rem
}
.confirm_unbind {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .4);
    z-index: 9;
}

.confirm_con {
    width: 90%;
    background: #fff;
    position: absolute;
    top: 30%;
    left: 50%;
    margin-left: -45%;
    border-radius:0.2133rem;
    padding: 0.6667rem 1.1733rem;
    box-sizing: border-box;
    text-align: center
}
.confirm_con .tit {
    font-size: 0.5333rem;
    color: #333;
    padding-bottom: 0.4rem
}
.confirm_con .tip{
    font-size: 0.4667rem;
    color: #333;
}
.confirm_con .cancel{
    width:2.6667rem;
    height:1.0667rem;
    border-radius:0.1067rem;
    font-size: 0.4533rem;
    color: #666;
    margin: auto;
    margin-top: 0.7733rem;
    border: 0.0133rem solid #666
}
.confirm_con .save {
    width:2.6667rem;
    height:1.0667rem;
    background:rgba(95,225,219,1);
    border-radius:0.1067rem;
    font-size: 0.4533rem;
    color: #fff;
    margin: auto;
    margin-top: 0.7733rem
}
</style>



