<template>
    <div class="update-form">
               <div class="inputbox">
                    <cell-group class="w2">
                        <field v-model="mobile" label="手机号" placeholder="请输入手机号" disabled/>
                    </cell-group>
                    <cell-group class="w2">
                        <field v-model="sms" label="验证码" center clearable placeholder="请输入短信验证码">
                        <Button
                            slot="button"
                            :disabled="t60!=60"
                            size="small"
                            type="primary"
                            @click="getAuthCode"
                        >{{t60==60?'获取验证码':'请'+t60+'秒后重试'}}</Button>
                        </field>
                    </cell-group>
                    <cell-group class="w2">
                        <field v-model="new_password" label="密码" placeholder="请设置新密码"/>
                    </cell-group>
                </div>
                <button class="btn" @click="confirm">提交</button> 
    </div>
</template>

<script>
import {Field, Button, cellGroup,Toast} from 'vant';

export default {
    components:{Field, Button, cellGroup,Toast},
    data(){
        return {
            staff_token: '',
            mobile: "",
            sms: "",
            t60: 60,
            new_password:'',
            error:false
        }
    },
    methods:{
        register(){
            this.$router.push({ path: "/register"});
        },
        choose(cur){
            this.cur=cur
        },
        gotokefu(){
            window.open('tel:0755-21002477')
        },
        //手机号验证
        isPoneAvailable($poneInput) {
        var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if (!myreg.test($poneInput)) {
                return false
                    this.error=true
            } else {
                return true;
            }
        },
         //获取验证码
        getAuthCode() {
        if (this.isPoneAvailable(this.mobile)) {
            this.api
            .sendSMS({
                mobile: this.mobile
            })
            .then(res => {
                if (res.code == 0) {
                Toast.success("验证码已成功发送，请注意查收");
                var timer = setInterval(() => {
                    this.t60--;
                    if (this.t60 == 0) {
                    this.t60 = 60;
                    clearInterval(timer);
                    }
                }, 1000);
                } else {
                Toast(res.contentEncrypt);
                }
            });
        } else {
            Toast("请输入正确的手机号");
        }
        },
        //验证码登录
        confirm() {
            if (!this.mobile) {
                Toast("请输入手机号");
                return;
            }
            if (!this.sms) {
                Toast("请输入验证码");
                return;
            }

            if (!this.new_password) {
                Toast("请输入新密码");
                return;
            }

        if (this.isPoneAvailable(this.mobile)) {

            this.api
            .updatePassword({
                mobile: this.mobile,
                sms_code: this.sms,
                new_password: this.new_password,
                staff_token: this.staff_token
            })
            .then(res => {
                    
                if (res.code != 0) { 
                    Toast(res.message)
                    return
                }

                Toast("修改成功!")

                setTimeout(() => {
                    this.$router.push({ path: 'set_Up' })
                }, 1500)
            });

            } else {
                Toast('请输入正确手机号')
            }
        }
    
    
    },
    mounted(){
        document.querySelector('body').setAttribute('style', 'background-color:white');
        this.staff_token = localStorage.getItem("staff_token")
        this.api
            .staffInfo({
                params: { staff_token: this.staff_token }
            })
            .then(res => {
                    
                if (res.code != 0) { 
                    Toast(res.message)
                    return
                }

                this.mobile = res.content.info.mobile
                if (!this.mobile) {
                    Toast('没有查到您的登录手机号，请联系客服')
                }
                
            });
    }
}
</script>

<style scoped>
.update-form{
    padding-top: 0.2rem;
    background: #fff
}
ul.tab-tilte{
    padding: 0 17%;
}
ul.tab-tilte li {
    margin: 0;
    padding: 0 0.6667rem;
    list-style: none;
    padding-bottom: 0.2667rem;
}
.tab-tilte li{
    float: left;
    text-align: center;
    font-size: 0.4267rem;
    color: #999;
    position: relative;
}
/* 点击对应的标题添加对应的背景颜色 */
.tab-tilte .active{  
    color: #5fe1db;
}
.tab-tilte .active::after{
    content: "";
    display: block;
    width: 80%;
    height: 0.0533rem;
    border-bottom: 0.0533rem solid #5fe1db;
    position: absolute;
    bottom: 0;
    left: 10%;
}
.tab-content ul li{
    margin: 0.2667rem 0.3333rem;
    background: #fff;
}
.inputbox {
    padding: 0.5333rem;
    font-size: 0.2133rem;
    padding-bottom: 0
}
.van-cell.van-field {
    padding: 0.4rem 0;
    border-bottom: 1px solid #eee;
}
.w2 .van-field__label {
    max-width: 2rem!important;
}
.van-button--primary {
    background-color: transparent;
    color: #bfc7cc;
    position: relative;
    border-color: transparent;
    font-size: 14px;
}
.van-button--primary::after {
      content: "";
      width: 1px;
      height: 18px;
      background-color: #ddd;
      position: absolute;
      top: 5px;
      left: -5px;
      z-index: 20;
    }
.van-button__text {
      color: #5fe1db;
}
.van-button--disabled .van-button__text {     
    color: #bfc7cc;   
}
[class*=van-hairline]::after{
    border: none
}
.btn {
    width: 95%;
    height: 1.1333rem;
    background-color: #5fe1db;
    border-radius: 0.0667rem;
    color: #fff;
    font-size: 0.4267rem;
    display: block;
    padding: 0;
    margin: auto;
    margin-top: 1.1333rem
}
</style>



