import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  state: {
    timeList1: [],
  },
  mutations: {
    increment: (state, index) => state.timeList1[index]--,
    decrement: (state, index) => state.timeList1[index]++,
    saveData: (state, list) => state.timeList1 = list,
  }
})



